*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}

body {
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(45deg, rgb(217, 217, 217) 40%, rgb(161, 161, 161) 100%)
}

h1 {
  font-size: 2rem;
}
h2 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1rem;
}

.container {

  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  // flex-direction: column;
  .main {
    background-color: #fff;
    display: flex;
    width: 1300px;
    height: 700px;
    border-radius: 40px;
    border: 1px solid black;
    &-nav {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &-list {
        // justify-self: normal;
        cursor: pointer;
        // border-radius: 4px;
        margin: 10px;
        transition: all 0.3s;


        h3 {
          padding-left: 5px;
        }
      }

      &-list:hover {
        border-left: 6px solid rgb(100, 100, 100);
        transition: all 0.3s;

      }

      margin-left: 5px;
      padding: 30px;
      // width: 40%;
      height: 100%;
      border-right: 1px solid black;
    }
    &-content {
      width: 100%;
      &-addition {
        width: 100%;
        height: 100%;
      }
    }
    &-text {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;

      h2 {
        min-height: 200px;
        width: 100%;
        word-wrap: normal;
        word-break: break-all;

        // justify-self:baseline;
      }
      [contenteditable] {
        outline: 0px solid transparent;
      }
      position: relative;

      &-wrapper {
        margin-bottom: 25px;
        display: flex;
        flex-direction: column;
        gap: 25px;
        h3 {
          position: absolute;
          top: 20px;
          right: 40px;
        
        }


      }
      &-addition {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &-input {
          width: 100%;
          textarea {
            // display: flex;
            // flex-direction: row;
            overflow: hidden;
            height: 200px;
            width: 49%;
            resize: none;
            font-family: "Montserrat";
            font-weight: 500;
            border: 1px solid rgb(144, 144, 144);
            border-radius: 5px;
            // border-right: 1px solid rgb(144, 144, 144);
            // border-bottom: 1px solid rgb(144, 144, 144);
            padding: 10px 0 0 30px;
          }
          textarea::placeholder {
            vertical-align: text-top;
            display: flex;
            height: 100%;
            // padding-left: 40px;
            // padding-top: 20px;
            align-items: flex-start;
            justify-content: baseline;
          }
          textarea:nth-child(1) {
            margin-right: 1%;
          }
        }
      }
      button {
        align-self: flex-end;
        margin-right: 50px;
        width: 200px;
        height: 2.5rem;
        border-radius: 20px;
        border: 0;
        background-color: rgb(181, 181, 181);
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        font-size: 1rem;
        transition: all 0.3s;
        cursor: pointer;
      }
      button:hover {
        color: white;
        background-color: rgb(48, 48, 48)
      }
      padding: 30px;
    }
  }
}

.active {
  border-left: 6px solid black;
}

.btn {
  width: 200px;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  cursor: pointer;
  background-color: rgb(181, 181, 181);
  transition: all 0.3s;
}
.btn:hover {
  color: white;
  background-color: rgb(48, 48, 48)
}

input::placeholder {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
}



